import qs from "qs"
import { axiosClient } from "@rp/react/axios-client"
import { CONTENT_PACKAGES_ROUTE } from "../content.constants"
import { ContentPackagesResponse } from "../content.types"

type GetContentPackagesProps = {
  offset: number
  pageCount: number
}

async function getContentPackages({
  offset,
  pageCount,
}: GetContentPackagesProps): Promise<ContentPackagesResponse> {
  const query = qs.stringify({ page: offset + 1, limit: pageCount })
  const { data } = await axiosClient.get<ContentPackagesResponse>(
    `${CONTENT_PACKAGES_ROUTE}/?${query}`,
  )

  return data
}

export { getContentPackages }
